import { SpaceLocation } from "./SpaceLocation";

// Part 1: Declare (5) Variables With Type
let prevspacecraftName: string = "Determination";
let prevspeedMph: number = 17500;
let kilometersToMars: number = 225000000;
let kilometersToTheMoon: number = 384400;
let prevmilesPerKilometer: number = 0.621;

// Part 2: Print Days to Mars
let milesToMars: number = kilometersToMars * prevmilesPerKilometer;
let hoursToMars: number = milesToMars / prevspeedMph;
let daysToMars: number = hoursToMars / 24;

// Code an output statement here (use a template literal):
let logDaysToMars: string = `
${prevspacecraftName} would take ${daysToMars} days to get to Mars.
`;

console.log(logDaysToMars);

// Part 3: Create a Function ("getDaysToLocation")
function prevgetDaysToLocation(kilometersAway: number): number {
  let milesTo = kilometersAway * prevmilesPerKilometer;
  let hoursTo = milesTo / prevspeedMph;
  let numOfDaysToLocation = hoursTo / 24;

  return numOfDaysToLocation;
}
// Move your output statement from part 2 here. Update the template literal to call
// the function and print the outputs for a Mars trip and a moon trip.
logDaysToMars = `
 ${prevspacecraftName} would take ${prevgetDaysToLocation(
  kilometersToMars
)} days to get to Mars.
`;

console.log(logDaysToMars);

let logDaysToMoon: string = `
 ${prevspacecraftName} would take ${prevgetDaysToLocation(
  kilometersToTheMoon
)} days to get to the Moon.
`;

console.log(logDaysToMoon);

// Part 4: Create a Spacecraft Class
// Create an instance of the class here:

class Spacecraft {
  milesPerKilometer: number = 0.621;
  craftName: string;
  speedMph: number;

  constructor(craftName: string, speedMph: number) {
    this.craftName = craftName;
    this.speedMph = speedMph;
  }
  getDaysToLocation(kilometersAway: number): number {
    let milesTo = kilometersAway * this.milesPerKilometer;
    let hoursTo = milesTo / this.speedMph;
    let numOfDaysToLocation = hoursTo / 24;

    return numOfDaysToLocation;
  }
  printDaysToLocation(location: SpaceLocation) {
    console.log(
      `${this.craftName} would take ${this.getDaysToLocation(
        location.kilometersAway
      )} days to get to ${location.name}.`
    );
  }
}

let spaceShuttle = new Spacecraft("Determination", 17500);

